
.main-layout {
    width: 100%;
    height: 93vh;
    display: flex;
    flex-direction: column;
    background-color: lightgray;
}

.new-button {
    height: 100%;
    width: 30%;
    margin-right: 10px;
    border-radius: 2rem;
    font-size: 250%;
}


/*--------Header----------*/

.header {
    width: 100%;
    height: 8vh;
    display: flex;
    justify-content: flex-end;
}

.header-link {
    display: block;
    text-align: center;
    text-decoration: none;
    width: 30%;
    height: 50%;
    border: 1px solid green;
    border-radius: 1rem;
    margin: 5% 5% 0 0;
}

.header-link > h3 {
    margin: 0 auto;
    color: green;
}


/*--------LISTS---------*/


.lists {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100vw;
    justify-content: space-between;
}

.new-item-button {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 7rem;
    height: 7rem;
    border-radius: 50%;
    position: sticky;
}

.new-item-button > svg {
    width: 2.5em;
    height: 2.5em;
}

.list-buttons {
    display: flex;
    justify-content: center;
    height: 20%;
}

.plIcon {

}

.list-plus-icon {
    width: 100%;
    height: 100%;
    color: green;
}

.mic-button {
    display: flex;
    justify-content: center;
    height: 100%;
    width: 100%;
}

/*----------List-------------*/

.product {
    z-index: 0;
}

.list-items {
    width: 100%;
    height: 60vh;
    background-color: khaki;
    overflow-y: scroll;
}

.save-button {
    margin-bottom: 5%;
    height: 30vh;
}
.save-button-none {
    display: none;
}

.hide-button {
    width: 40%;
    align-self: center;
}

.delete-button {
    border-radius: 0.5rem;
    background-color: crimson;
}

.edit-button {
    border-radius: 0.5rem;
}

.done-button {
    border-radius: 50px;
}

.product-text-custom {
    text-decoration: line-through;
}

.one-item {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 5px 0 5px 5px;
}

.one-item-buttons {
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin-right: 20px;
}

/*---form----------------------------------*/
.input-field {
    margin: 0 auto;
    display: none;
    /*opacity: 0;*/
    /*z-index: -10;*/

    flex-direction: column;
    width: 80%;
    /*align-items: center;*/
    /*background-color: silver;*/
}

.show-form {
    display: flex;
    position: absolute;
    top: 40%;
    left: 8%;
    opacity: 1;
    z-index: 2;
}

.item-form {
    display: flex;
    flex-direction: column;
    height: 30vh;
    width: 100%;
}

.item-form > input {
    display: flex;
    width: 98%;
    height: 160%;
    font-size: 14px;
    margin-bottom: 10%;
    border-radius: 0.4rem;
}

.item-form > button {
    /*height: 20%;*/
    border-radius: 1.3rem;
    font-size: 1.5em;
}

/*--------AddNewPage-------*/

.add-new-page, .link-plus {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
}

.plus-icon {
    align-self: center;
    width: 90%;
    height: 90%;
    color: green;
}